@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background-color: #000000;
}

/* Header Css */
.navbar-font {
  font-family: "Gotham";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  color: #ffffff;
}

.active {
  margin: 10px;
  background: #fff7ff;
  color: #000000;
  width: 110px;
  height: 50px;
  border-radius: 8px;
}
.marquee {
  height: 25px;

  /* overflow: hidden; */
  position: relative;
}

.marquee div {
  width: 100%;
  height: 150px;
  position: absolute;
  overflow: hidden;
  display: flex;
  align-items: center;
  animation: marquee 5s linear infinite;
}

.marquee span {
  color: white;
  float: left;
  width: 40%;
}

@keyframes marquee {
  0% {
    transform: translate3d(0%, 0, 0);
  }

  100% {
    transform: translate3d(-100%, 0, 0);
  }
}

.background-home {
  background: url("components/Assets/Images/PricePool.png");
}

/* Loader */

.loader {
  width: 50px; /* control the size */
  aspect-ratio: 1;
  display: grid;
  -webkit-mask: conic-gradient(from 15deg, #0003, #000);
  mask: conic-gradient(from 15deg, #0003, #000);
  animation: load 1s steps(12) infinite;
}
.loader,
.loader:before,
.loader:after {
  background: radial-gradient(circle closest-side, currentColor 90%, #0000 98%)
      50% -150%/20% 80% repeat-y,
    radial-gradient(circle closest-side, currentColor 90%, #0000 98%) -150% 50%/80%
      20% repeat-x;
}
.loader:before,
.loader:after {
  content: "";
  grid-area: 1/1;
  transform: rotate(30deg);
}
.loader:after {
  transform: rotate(60deg);
}
@keyframes load {
  from {
    transform: rotate(0turn);
  }
  to {
    transform: rotate(1turn);
  }
}

.marquee-container .overlay::before,
.marquee-container .overlay::after {
  --gradient-color: none;
}
