.rwd-table {
  margin: auto;
  min-width: 100%;
  max-width: 100%;
  border-collapse: collapse;
}

.rwd-table tr:first-child {
  border-top: none;
  background: #2c2c2c;
  color: #fff;
}

.rwd-table tr {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  /* background-color: #f5f9fc; */
}

/* .rwd-table tr:nth-child(odd):not(:first-child) {
  background-color: #ebf3f9;
} */

.rwd-table th {
  display: none;
}

.rwd-table td {
  display: block;
}

.rwd-table td:first-child {
  margin-top: 0.5em;
}

.rwd-table td:last-child {
  margin-bottom: 0.5em;
}

.rwd-table td:before {
  content: attr(data-th) ": ";
  font-weight: bold;
  width: 120px;
  display: inline-block;

  /* color: #000; */
}

.rwd-table th,
.rwd-table td {
  /* text-align: left; */
}

.rwd-table {
  color: #fff;
  border-radius: 0.4em;
  overflow: hidden;
}

.rwd-table tr {
  border-color: #353434;
  text-align: center;
}
.rwd-table th {
  height: 100px;
}
.rwd-table th,
.rwd-table td {
  padding: 0.5em 1em;
}
/* .table-width {
  display: flex !important;
  gap: 5px;
  align-items: center;
  justify-content: center;
} */
@media screen and (max-width: 601px) {
  .rwd-table tr:nth-child(2) {
    border-top: none;
  }
  .rwd-table th,
  .rwd-table td {
    text-align: left;
  }
  .table-width {
    display: flex !important;
    gap: 5px;
    align-items: center;
    justify-content: flex-start;
  }
}
@media screen and (min-width: 600px) {
  .rwd-table tr:hover:not(:first-child) {
    /* background-color: #d8e7f3; */
    /* color: #000; */
  }
  .rwd-table tr {
    border-color: #353434;
  }
  .table-width {
    display: flex !important;
    gap: 5px;
    align-items: center;
    justify-content: center;
  }

  .rwd-table td:before {
    display: none;
  }
  .rwd-table th,
  .rwd-table td {
    display: table-cell;
    padding: 0.25em 0.5em;
  }
  .rwd-table th:first-child,
  .rwd-table td:first-child {
    padding-left: 0;
  }
  .rwd-table th:last-child,
  .rwd-table td:last-child {
    padding-right: 0;
  }
  .rwd-table th,
  .rwd-table td {
    padding: 1em !important;
  }
}
