.flex-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-content: space-around;
  width: 100%;
}

.flex-container > div {
  color: white;

  margin: 10px;
  flex: 0 1 20%;
}

@media (max-width: 800px) {
  .flex-container > div {
    max-width: 75%;
    flex: 0 1 30%;
  }
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media (max-width: 600px) {
  .flex-container > div {
    max-width: 100%;
    flex: 0 1 100%;
  }
}
.card {
  margin: 0 30px;
  padding: 20px;
  overflow: hidden;
  height: 100px;
}
